import { NextResponse } from "next/server";
import connectDB from "@/app/lib/db";
import Cart from "@/app/models/cartModel";
import Order from "@/app/models/orderModel";

export async function POST(req) {
  try {
    await connectDB();

    const { userId, address, paymentMethod } = await req.json();

    if (!userId || !address || !paymentMethod) {
      return NextResponse.json(
        { message: "Missing required fields" },
        { status: 400 }
      );
    }

    // ✅ Fetch user cart and populate product details
    const cart = await Cart.findOne({ userId }).populate("items.productId");

    if (!cart || cart.items.length === 0) {
      return NextResponse.json({ message: "Cart is empty" }, { status: 400 });
    }

    // ✅ Calculate total amount
    const totalAmount = cart.items.reduce(
      (acc, item) => acc + item.productId.price * item.quantity,
      0
    );

    // ✅ Create new order
    const newOrder = new Order({
      userId,
      items: cart.items.map((item) => ({
        productId: item.productId._id,
        quantity: item.quantity,
        price: item.productId.price,
      })),
      totalAmount,
      address,
      paymentMethod,
      status: "Pending",
    });

    await newOrder.save();

    // ✅ Clear cart after placing order
    cart.items = [];
    await cart.save();

    return NextResponse.json(
      { message: "Order placed successfully", order: newOrder },
      { status: 201 }
    );
  } catch (error) {
    console.error("Order POST error:", error);
    return NextResponse.json(
      { message: "Error placing order", error: error.message },
      { status: 500 }
    );
  }
}
